@font-face {
  font-family: 'Candyshop';
  src: local('Candyshop'), url(./fonts/Candyshop.ttf) format('truetype');
}
@font-face {
  font-family: 'YuseiMagic-Regular';
  src: local('Candyshop'), url(./fonts/YuseiMagic-Regular.ttf) format('truetype');
}
@keyframes slide-in{
  0% {transform:translateX(-30%)}
  20% {transform:translateX(50%)}
  40% {transform:translateX(100%)}
  60% {transform:translateX(55%)}
  100% {transform:translateX(0%)}
}
.article{
  width:70vw;
  margin:1.5em auto;
  padding:2em 2em;
}
.article-low-padding{
  width:70vw;
  margin:1.5em auto;
  padding:0;
}
.article h2{
  color: #3e8bca;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(223, 228, 234,1.0)
}

.btn-main{
  cursor:pointer;
  width:200px;
  height:50px;
  font-size: 1em;
  background-color: #82ccdd;
  border:0;
  border-radius:5px;
}
.btn-main:hover{
  background-color: #60a3bc;
}
.button-show{
  display:flex;
  flex-wrap: wrap;
}
.bg-container{
  overflow: hidden;
  position:absolute;
  top:0;
  z-index: -1;
}
.banner-title{
  /* margin-bottom: 5em; */
  box-sizing: border-box;
  transform: translateY(-1em);

}
.band-wrapper{
  display:flex;
  align-items: center;
  justify-content: center;
}
.blog-list{
  padding:0;
  height:100vh;
}
.blog-list li{
  box-sizing: border-box;
  list-style: none;
  text-align: start;
  width:100%;
  padding:.5em 1em;
  border:1px solid #ddd;
  color:#3e8bca
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.contact-card{
  padding:1em 1em;
  box-sizing: border-box;
}
.circle{
  width:1em;
  height:1em;
  background-color: #3e8bca;
  border-radius: 100%;
  float: left;
  position:absolute;
  left:-1.2em;
  top:0.3em;
}
#close-menu{
  padding-left:0.5em;
  margin:0.2em 0;
}
.drop-down{
  position:relative
}
.drop-down-item{
  position:absolute;
  background-color: rgba(29, 31, 34, .8);
  width:8em;
  height:150px;
  top:43px;
  left:-15px;
  transition:all 0.5s;
  overflow: hidden;
  padding: 0 0;
}
.drop-down-item>ul{
  padding:0
}
.drop-down-item li{
  list-style-type:none;
  padding:1em 1em 0 1em;
  border-bottom: #d5d5d5 solid 1px;
}
.exp li{
  position:relative;
  margin-left:0;
  padding-left:.5em;
  top:-0.5em;
}
.exp{
  position:relative;
  border-left:#3e8bca solid 3px;
}
.exp h5{
  display: inline-block;
  margin-right: 10px;
  margin-top: 0px;

}
.gh-btn{
  display:inline-block;
  border: 1px solid #d5d5d5;
  background-color: #eee;
  border-radius: 4px;
  padding:0 5px;
}
.gh-icon{
  width:14px;
  height:14px;
  background-size: 14px 14px;
  display:inline-block;
  margin-right: 5px;
}

hr{
  border:1.4px solid rgba(58, 60, 63, 1)
}
.header{
  box-sizing: border-box;
  padding:0.5em 1em;
  text-align: start;
  width:100%;
  font-weight: 500;
  background-image:linear-gradient(to bottom,#f5f5f5 0,#e8e8e8 100%);
  border-radius: 5px;
}

.intro{
  color:#6a89cc;
  font-family: "YuseiMagic-Regular";
  position:relative;
  max-width: 800px;
  margin:3em auto 0 auto;
}

.icon-button{
  cursor:pointer;
}
.navbar{
  /* height:50px;  */
  margin:0;
  color:whitesmoke;
  background-color: rgba(29, 31, 34, 0.5)
}
.nav-container{
  display:flex;
  margin:0 15%;
  flex-direction: row;
  background-color: transparent;
  justify-content: space-between;
  align-items: center;
  
}
.md{
  text-align: start;
}
.md pre{
  background-color: #f7f7f7;
  overflow-X: scroll;
}
.menu{
  list-style-type:none;
  margin:0;
}
.menu-container{
  display:flex;
  justify-content: center;
}
.menu>li{
  display:inline-block;
  margin:1.5em 1em;
  cursor: pointer;
  line-height: 100%;
}
a{
  text-decoration: none;
  color:inherit
}
.menu a:hover{
  color:burlywood;
}
.margin-top30{
  margin-top: 4em;
}
.main{
  text-align: start;
}
.main dd{
  margin-left: 0;
}
.main li{
  list-style: none;
}
.main ul{
  padding-left:10px
}
.main dt{
  font-size: 24px;
  line-height: 30px;
  color:#60a3bc;
  border-bottom: 2px solid #3e8bca;
  margin-top: 10px;
}
.main dd{
  line-height: 27px;
}
.main h3{
  color: #3e8bca
}
.main p{
  margin:.5em;
}
.logo{
  cursor:pointer;
}
.link-btn{
  display:inline-block;
  padding:0.5em 0.7em;
  background-color: #dbf0f8;
  border-radius: 20px;
  transition: 0.3s ease;
  margin:0.2em 0.2em
}
.link-btn:hover{
  transform: scale(1.1);
}
.link-icon{
  display:inline-block;
  width:16px;
  height:16px;
  background-size: 16px 16px;
  margin-right: 5px;
  line-height: 20px;
  
}
.photo{
  margin:auto;
  width:200px;
  height:200px;
  overflow: hidden;
  border-radius: 100%;
}
.paper{
  background-color:rgb(252, 250, 247);
  box-shadow: -3px 3px 8px grey;
  border-radius: 5px;
}
.side-nav{
  position:absolute;
  z-index: 1;
  transition:all 0.5s;
  background-color: rgba(29, 31, 34, 1);
  right:0;
  top:0;
}
.show-case{
  display:flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  max-width: 90vw;
  margin:auto;
  margin-top:2em;
}
.text-start{
  text-align: start;
  margin:0.3em 1em;

}
.text-center{
  text-align: center;
}
.title{
  display: inline-block;
  font-family: Candyshop;
  font-size: 4em;
  color:#535dbb;
  animation:slide-in .5s ease 1;
}
.verticle-menu{
  padding:0;
  width:30vw;
  list-style-type: none;
}
.verticle-menu>li{
  box-sizing: border-box;
  padding:0 2em;
  width:100%;
  margin:1em 0;
  text-align: start;
}
